.page-main__wrapper{
  display: flex;
  width: 320px;
  height: 488px;
  margin: 0 auto;
  flex-direction: column;
  justify-content: space-between;
  background: url(../img/hero-mobile@1x.jpg) #f1f1f1;
  background-position: -127px 355px;
}
.page-main__wrapper--grey{
  width: 100%;
  background: #f1f1f1;
}
.page-main__headline{
  width: 260px;
  text-align: center;
  margin: 75px auto 0;
  color: #000000;
  font-size: 27px;
  line-height: 30px;
  font-weight: bold;
}
.page-main__wrapper--grey .page-main__headline{
  margin: 0 auto;
  padding: 75px 0 80px;
}
.page-main__form-desctription{
  font-weight: normal;
  font-size: 13px;
  line-height: 24px;
  margin: 22px 0 5px;
}
.page-main__line--grey{
  display: flex;
  width: 100%;
  height: 6px;
  background: url('../img/bg-zigzag-line.svg') repeat-x;
  background-size: auto 6px;
  background-position-x: 50%;
}
.page-main__line--reviews{
  margin: 45px 0 0;
}
.page-main__item-list{
  margin: 30px 0 0;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}
@media (min-width: @tablet-width) {
  .page-main__wrapper{
    display: flex;
    width: 668px;
    padding: 0 50px;
    height: 730px;
    background: url(../img/hero-tablet@1x.jpg) #f1f1f1;
    background-position: -190px 0;
  }
  .page-main__headline{
    margin: 162px auto 0;
    font-size: 43px;
    line-height: 50px;
    width: 500px;
  }
  .page-main__form-desctription{
    font-weight: normal;
    font-size: 17px;
    line-height: 30px;
  }
  .page-main__line--white{
    position: relative;
    display: flex;
    width: 100%;
    height: 23px;
    margin: 0;
    background: url('../img/bg-zigzag-fill.svg') repeat-x;
    background-position-x: 50%;
    background-size: 768px 25px;
  }
  .page-main__line--grey{
    height: 17px;
    background-size: auto 17px;
  }
  .page-main__line--item{
    margin: 55px 0;
  }
  .page-main__line--reviews{
    margin: 68px 0 0;
  }
}
@media (min-width: @desktop-width) {
  .page-main__headline{
    margin: 162px auto;
    width: 700px;
    font-size: 53px;
    line-height: 57px;
  }
  .page-main__wrapper{
    display: flex;
    width: 1050px;
    padding: 0 50px;
    height: 745px;
    background: url(../img/hero-desktop@1x.jpg) #f1f1f1;
    background-position: 0;
  }
  .page-main__wrapper--grey .page-main__headline{
    padding: 100px 0 130px;
  }

  .page-main__line--white{
    height: 28px;
    background-size: 768px 34px;
  }
  .page-main__line--grey{
    height: 23px;
    background-size: auto 23px;
  }
  .page-main__line--item{
    margin: 75px 0 60px;
  }
  .page-main__line--reviews{
    margin: 60px 0 70px;
  }
  .page-main__item-list{
    flex-direction: row;
  }
}
